<template>
  <div class="echarts-main">
    <div class="ech-model-1">
      <div id="myChart" :style="{width: '100%', height: '300px'}"></div>
    </div>
    <div class="ech-model-1 map">
      <div id='chart' :style="{width: '100%', height: '300px'}"></div>
    </div>
    <div class="ech-model-1">
      <div id="myChart1" :style="{width: '100%', height: '300px'}"></div>
    </div>
    <div class="ech-model-1">
      <div id="myChart2" :style="{width: '100%', height: '300px'}"></div>
    </div>
    <div class="ech-model-1">
      <div id="myChart3" :style="{width: '100%', height: '300px'}"></div>
    </div>
  </div>
</template>

<script>

  export default {
    data() {
      return {
      }
    },
    mounted() {
      // 
      this.drawLine()
      // 
      this.drawChart()
      // 
      this.drawLine1()
      // 
      this.drawLine2()
      // 
      this.drawLine3()
    },
    methods:{
      // 
      drawLine(){
        // domecharts
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        // 
        myChart.setOption({
          title: { text: '' },
          tooltip: {},
          xAxis: {
            data: ["","","","","",""]
          },
          yAxis: {},
          series: [{
            name: '',
            type: 'bar',
            data: [5, 20, 36, 10, 10, 20]
          }]
        });
      },
      // 
      drawChart () {
        let that = this
        // domecharts
        let chart = this.$echarts.init(document.getElementById('chart'))
        // 
        window.addEventListener('resize', function () {
          chart.resize()
        })
        // 
        chart.on('click', function (res) {
          // 
          // that.$router.push({ path: '/provincechart', query: { cityName: res.data.name } })
        })
        // 
        chart.setOption({
          // 
          title: {
            text: '', //  \n 
            top: 20, //  : 'top', 'middle', 'bottom' 
            left: 'center', // : 'left', 'center', 'right' 
            textStyle: { // 
              fontSize: 24,
              fontWeight: 600,
              color: '#fff'
            }
          },
          // 
          tooltip: {
            trigger: 'item', // , 
            // 
            //    -> value: number | Array
            formatter: function (val) {
              if (val.data.value < 10000) {
                return val.data.name + '<br>: ' + val.data.value + ''
              } else {
                return val.data.name + '<br>: ' + (val.data.value / 10000).toFixed(2) + ''
              }
            }
          },
          // 
          visualMap: {
            type: 'continuous', // continuous   piecewise 
            show: true, //  visualMap-continuous   false
            //  visualMapContinuous /'min'/'max' 
            // [visualMap.min, visualMax.max] 
            min: 0,
            max: 11300,
            // 
            textStyle: {
              fontSize: 14,
              color: '#fff'
            },
            realtime: false, // 
            calculable: true, // 
            //   
            inRange: {
              color: ['#9fb5ea', '#e6ac53', '#74e2ca', '#85daef', '#9feaa5', '#5475f5'] // 
            }
          },
          series: [
            {
              type: 'map', // 
              // tooltiplegend   setOption 
              name: '',
              mapType: 'china', // 
              //  
              //  'scale'  'move'  true 
              roam: false,
              // 
              label: {
                show: false // 
              },
              //  
              itemStyle: {
                areaColor: '#7B68EE', //  visualMapareaColor
                borderWidth: 0.5, //   0 
                borderColor: '#000', //   color
                borderType: 'solid' //  'solid', 'dashed', 'dotted'
              },
              // 
              emphasis: {
                label: {
                  show: true, // 
                  color: '#fff' //   'auto'
                },
                itemStyle: {
                  areaColor: '#FF6347' // 
                }
              },
              //  
              data: [
                { name: '', value: 2154, lng: 116.405285, lat: 39.904989 },
                { name: '', value: 1560, lng: 117.190182, lat: 39.125596 },
                { name: '', value: 2424, lng: 121.472644, lat: 31.231706 },
                { name: '', value: 3102, lng: 106.504962, lat: 29.533155 },
                { name: '', value: 7556, lng: 114.502461, lat: 38.045474 },
                { name: '', value: 9605, lng: 113.665412, lat: 34.757975 },
                { name: '', value: 4830, lng: 102.712251, lat: 25.040609 },
                { name: '', value: 4359, lng: 123.429096, lat: 41.796767 },
                { name: '', value: 3773, lng: 126.642464, lat: 45.756967 },
                { name: '', value: 6899, lng: 112.982279, lat: 28.19409 },
                { name: '', value: 6324, lng: 117.283042, lat: 31.86119 },
                { name: '', value: 10000, lng: 117.000923, lat: 36.675807 },
                { name: '', value: 2487, lng: 87.617733, lat: 43.792818 },
                { name: '', value: 8051, lng: 118.767413, lat: 32.041544 },
                { name: '', value: 5737, lng: 120.153576, lat: 30.287459 },
                { name: '', value: 4648, lng: 115.892151, lat: 28.676493 },
                { name: '', value: 5917, lng: 114.298572, lat: 30.584355 },
                { name: '', value: 4926, lng: 108.320004, lat: 22.82402 },
                { name: '', value: 2637, lng: 103.823557, lat: 36.058039 },
                { name: '', value: 3718, lng: 112.549248, lat: 37.857014 },
                { name: '', value: 2534, lng: 111.670801, lat: 40.818311 },
                { name: '', value: 3864, lng: 108.948024, lat: 34.263161 },
                { name: '', value: 2704, lng: 125.3245, lat: 43.886841 },
                { name: '', value: 3941, lng: 119.306239, lat: 26.075302 },
                { name: '', value: 3600, lng: 106.713478, lat: 26.578343 },
                { name: '', value: 11300, lng: 113.280637, lat: 23.125178 },
                { name: '', value: 603, lng: 101.778916, lat: 36.623178 },
                { name: '', value: 344, lng: 91.132212, lat: 29.660361 },
                { name: '', value: 8341, lng: 104.065735, lat: 30.659462 },
                { name: '', value: 688, lng: 106.278179, lat: 38.46637 },
                { name: '', value: 934, lng: 110.33119, lat: 20.031971 },
                { name: '', value: 2358, lng: 121.509062, lat: 25.044332 },
                { name: '', value: 745, lng: 114.173355, lat: 22.320048 },
                { name: '', value: 63, lng: 113.54909, lat: 22.198951 },
                { name: '', value: 271.13, lng: 114.252615, lat: 15.86029 }
              ]
            }
          ]
        })
        // 
       /* let index = -1
        setInterval(function () {
          // 
          chart.dispatchAction({
            type: 'hideTip',
            seriesIndex: 0,
            dataIndex: index
          })
          // 
          chart.dispatchAction({
            type: 'showTip',
            seriesIndex: 0,
            dataIndex: index + 1
          })
          // 
          chart.dispatchAction({
            type: 'downplay',
            seriesIndex: 0,
            dataIndex: index
          })
          // 
          chart.dispatchAction({
            type: 'highlight',
            seriesIndex: 0,
            dataIndex: index + 1
          })
          index++
          if (index > 34) {
            index = -1
          }
        }, 2000)*/
      },
      // 
      drawLine1(){
        // 
        let option = {
          title: {
            text: ''
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: ['', '', '', '', '']
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          //
          //
          toolbox: {
            show:false,
            feature: {
              saveAsImage: {}
            }
          },
          //x
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: ['', '', '', '', '', '', '']
          },
          //yy
          yAxis: {
            type: 'value'
          },
          series: [
            {
              name: '',
              type: 'line',
              stack: '',
              data: [120, 132, 101, 134, 90, 230, 210]
            },
            {
              name: '',
              type: 'line',
              stack: '',
              data: [220, 182, 191, 234, 290, 330, 310]
            },
            {
              name: '',
              type: 'line',
              stack: '',
              data: [150, 232, 201, 154, 190, 330, 410]
            },
            {
              name: '',
              type: 'line',
              stack: '',
              data: [320, 332, 301, 334, 390, 330, 320]
            },
            {
              name: '',
              type: 'line',
              stack: '',
              data: [820, 932, 901, 934, 1290, 1330, 1320]
            }
          ]
        };
        // domecharts
        let myChart = this.$echarts.init(document.getElementById('myChart1'))
        // 
        myChart.setOption(option)
      },
      // 
      drawLine2() {
        let option = {
          title: {
            text: '',
            subtext: '',
            left: ''
          },
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b} : {c} ({d}%)'
          },
          legend: {
            orient: 'vertical',
            left: 'left',
            data: ['', '', '', '', '']
          },
          series: [
            {
              name: '',
              type: 'pie',
              radius: '55%',
              center: ['50%', '60%'],
              data: [
                {value: 335, name: ''},
                {value: 310, name: ''},
                {value: 234, name: ''},
                {value: 135, name: ''},
                {value: 1548, name: ''}
              ],
              emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        };
        // domecharts
        let myChart = this.$echarts.init(document.getElementById('myChart2'))
        // 
        myChart.setOption(option)
      },
      // 
      drawLine3() {
        let option = {
          title: {
            text: '',
            subtext: ''
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c}%"
          },
          toolbox: {
            show:false,
            feature: {
              dataView: {readOnly: false},
              restore: {},
              saveAsImage: {}
            }
          },
          legend: {
            data: ['','','','','']
          },

          series: [
            {
              name:'',
              type:'funnel',
              left: '10%',
              top: 60,
              //x2: 80,
              bottom: 60,
              width: '80%',
              // height: {totalHeight} - y - y2,
              min: 0,
              max: 100,
              minSize: '0%',
              maxSize: '100%',
              sort: 'descending',
              gap: 2,
              label: {
                show: true,
                position: 'inside'
              },
              labelLine: {
                length: 10,
                lineStyle: {
                  width: 1,
                  type: 'solid'
                }
              },
              itemStyle: {
                borderColor: '#fff',
                borderWidth: 1
              },
              emphasis: {
                label: {
                  fontSize: 20
                }
              },
              data: [
                {value: 60, name: ''},
                {value: 40, name: ''},
                {value: 20, name: ''},
                {value: 80, name: ''},
                {value: 100, name: ''}
              ]
            }
          ]
        };
        // domecharts
        let myChart = this.$echarts.init(document.getElementById('myChart3'))
        // 
        myChart.setOption(option)
      }
    }
  }
</script>

<style scoped lang="scss">
  .echarts-main{
    display: flex;
    flex-direction: column;
    .ech-model-1{
      background: #beb5ae;
      margin: 0.5rem;
      box-sizing: border-box;
      &.map{
        background: #5f5f5f;
      }
    }
  }
</style>



