<template>
  <div class="user-main">
    <!--  -->
    <div class="user-main-up">
      <div class="user-up-f1">
        <img class="user-up-img" src="https://cn.bing.com/th?id=OHR.NarniaForest_ZH-CN8466850438_UHD.jpg&rf=LaDigue_UHD.jpg&pid=hp&w=2880&h=1620&rs=1&c=4"/>
        <div @click="getLeftMenu()" class="user-up-li"></div>
      </div>
      <div class="user-up-f2">Jack</div>
      <div class="user-up-f3">
        <span> </span>/
        <span></span>/
        <span>{{week}}</span>/
        <span>{{year}}</span>
      </div>
    </div>
    <!--  -->
    <div class="user-manu">
      <van-grid>
        <van-grid-item icon="like-o" text="" @click="toPage"/>
        <van-grid-item icon="fire-o" text="" @click="toPage"/>
        <van-grid-item icon="gift-o" text="" @click="toPage"/>
        <van-grid-item icon="service-o" text="" @click="toPage"/>
      </van-grid>
    </div>
    <!--  -->
    <div class="user-main-bot">
      <div class="user-plan-tit"></div>
      <div class="user-plan-cen" v-for="(item,index) in list" :key="index">
        <div class="user-plan-date">{{item.year}}</div>
        <div class="user-main-plan">
          <div class="user-plan-model" v-for="(it,index) in item.infoList" :key="index">
            <div class="user-plan-model-l">
              <span>{{it.date}}</span>
              <div><div>...........................</div></div>
            </div>
            <div class="user-plan-model-r">
              <h2>{{it.title}}</h2>
              <p>{{it.cen}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="user-plan-add">
      <div @click="showPopup">
        <van-icon name="plus" />
      </div>
    </div>
    <!--  -->
    <van-popup v-model="show" closeable>
      <div class="user-pop-main">
        <div><van-field v-model="title" placeholder=",10" maxlength="10"/></div>
        <div><van-field v-model="cen" placeholder=",25" maxlength="25"/></div>
        <div @click="inputClick"><van-field v-model="date" placeholder="" readonly /></div>
        <van-popup safe-area-inset-bottom get-container="body" v-model="dateShow" position="bottom">
          <van-datetime-picker @confirm="dateConfirm" @cancel="dateCancel" v-model="currentTime" type="time" title="" :min-hour="1" :max-hour="24"/>
        </van-popup>
        <div><van-button @click="toSubmit()" round block type="info" native-type="submit"></van-button></div>
      </div>
    </van-popup>
    <!--  -->
    <van-popup v-model="leftShow" closeable position="left" :style="{ width: '45%',height: '100%' }">
      <div class="model-main">
        <van-cell-group>
          <van-cell title="" value="100"/>
        </van-cell-group>
        <van-cell-group>
          <van-cell title=""/>
        </van-cell-group>
      </div>
    </van-popup>
  </div>
</template>

<script>
  import {dateUtil} from '@/assets/js/util'
  import { Toast } from 'vant';
  export default {
    data () {
      return {
        show: false,
        currentTime: '',
        dateShow: false,
        title: '',
        cen: '',
        date: '',
        year: '',
        week: '',
        leftShow: false,
        list: [
          {
            year: '2021-01-04',
            infoList: [
              {title:'',cen:'',date:'7:30'},
              {title:'',cen:'',date:'6:30'}
            ]
          },
          {
            year: '2021-01-03',
            infoList: [
              {title:'',cen:'teamLab',date:'11:30'}
            ]
          }
        ]
      }
    },
    methods: {
      // 
      showPopup() {
        this.show = true;
      },
      // 
      inputClick() {
        this.dateShow = true;
      },
      // 
      dateConfirm(value) {
        // 
        let flag = this.toComTime(value)
        if (!flag) {
          this.getToast('')
          return
        }
        this.date = value
        this.dateShow = false;
      },
      //  
      dateCancel(){
        this.dateShow = false;
      },
      // 
      toSubmit() {
        // 
        if (this.title === '') {
          this.getToast('')
          return
        }
        if (this.cen === '') {
          this.getToast('')
          return
        }
        if (this.date === '') {
          this.getToast('')
          return
        }
        // 
        this.toAddList()
        this.show = false;
        // 
        this.title=''
        this.cen=''
        this.date=''
      },
      //  list
      toAddList() {
        let obj = {
          title: this.title,
          cen: this.cen,
          date: this.date
        }
        //  - 
        // List infoList
        let list = this.list
        if (list[0].year !== this.year) {
          let info = {
            year: this.year,
            infoList: []
          }
          list.unshift(info);
        }
        list[0].infoList.unshift(obj)
        console.log('1-', list[0].infoList)
        //   
        let newList = this.getNewList(list[0].infoList)
        console.log('2-', newList)
        list[0].infoList = newList
        this.list = list
        console.log('3-',this.list)
      },
      // 
      getNewList(list) {
        for (let i=0;i<list.length-1;i++) {
          for (let j=0;j<list.length-1-i;j++) {
            // 
            let a = dateUtil.toChangeStr(list[j].date)
            let b = dateUtil.toChangeStr(list[j+1].date)
            if ( parseInt(a) < parseInt(b) ) {
              let temp = list[i];
              list[j] = list[j + 1];
              list[j + 1] = temp;
            }
          }
        }
        return list
        // //forfor
        // //
        // var array=[10,20,9,8,79,65,100];
        // //
        // for ( var i=0;i<array.length-1;i++){
        //   //=-1-
        //   for (var j=0;j<array.length-1-i;j++) {
        //     if (array[j] > array[j + 1]) {
        //       var temp = array[i];
        //       array[j] = array[j + 1];
        //       array[j + 1] = temp;
        //     } //end if
        //   }//end for 
        // } //end for 
        // console.log(array);
      },
      // 
      getTime(value,hour,minu) {
        //    
        let a = dateUtil.toChangeStr(value)
        let b = dateUtil.toChangeStr(hour)
        //  ,+0
        if (minu.toString().length < 2){
          minu = '0'+minu.toString()
        }
        let one = parseInt(a)
        let two = parseInt(b+minu.toString())
        // console.log(a,b,minu,one,two)
        //  a = b+c  0; a > b+c  1; a < b+c  -1;   - 
        if (one === two) {
          return 0;
        } else if(one > two) {
          return 1;
        } else if(one < two) {
          return -1;
        } else {
          return null;
        }
      },
      //  
      toComTime(value) {
        let now = new Date();
        let year = now.getFullYear(); //
        let month = now.getMonth();//
        let date = now.getDate();//
        let day = now.getDay();//
        let hour = now.getHours();//
        let minu = now.getMinutes();//
        let sec = now.getSeconds();//
        let MS = now.getMilliseconds();//
        let flag = this.getTime(value,hour,minu)
        if (flag === 0) {
          // 
          return true;
        } else if(flag === 1) {
          // >
          return true;
        } else if(flag === -1) {
          // <
          return false;
        } else {
          // 
          return false;
        }
      },
      // toast
      getToast(mes='', dur='1500') {
        Toast({
          message: mes,
          duration: dur
        });
      },
      //  
      getDateTime() {
        let now = new Date();
        let day = now.getDay();//
        this.year = dateUtil.formatDateStr(now)
        switch (day) {
          case 1:
            this.week = ''
            return this.week
          case 2:
            this.week = ''
            return this.week
          case 3:
            this.week = ''
            return this.week
          case 4:
            this.week = ''
            return this.week
          case 5:
            this.week = ''
            return this.week
          case 6:
            this.week = ''
            return this.week
          case 7:
            this.week = ''
            return this.week
          default:
            return;
        }
      },
      // 
      getLeftMenu(){
        this.leftShow = true
      },
      // 
      toPage() {
        this.$router.push({path:"/achievement"});
      }
    },
    mounted(){
      this.getDateTime()
    }
  }
</script>

<style scoped lang="scss">
  .user-main{
    color: #ffffff;
    .user-main-up{
      padding: 0.6rem 0.6rem 1rem;
      background: #353535;
      border-radius: 0 0 1.2rem 1.2rem;
      .user-up-f1{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        .user-up-img{
          width: 2rem;
          height: 2rem;
          object-fit: cover;
          border-radius: 50%;
        }
        .user-up-li{
          width: 1.2rem;
          background-color: #fff;
          border-top: 0.15rem solid #fff;
          border-bottom: 0.15rem solid #fff;
          background-clip: content-box;
          height: 0.15rem;
          padding: 0.3rem 0 0.3rem 0.3rem;
        }
      }
      .user-up-f2{
        font-size: 1rem;
        margin: 0.5rem 0 0.5rem;
      }
      .user-up-f3{
        margin: 0 0 0.2rem;
        font-size: 0.7rem;
        span{
          margin: 0 0.2rem;
        }
      }
    }
    .user-manu {
        padding: .8rem;
        color: #ff863f;
        .van-grid {
          border-radius: .2rem;
          overflow: hidden;
          cursor: pointer;
          /deep/ .van-grid-item__text {
            color: #ff9133;
          }
        }
    }
    .user-main-bot{
      padding: 0.8rem;
      .user-plan-tit{
        padding: 0 0 0.8rem;
      }
      .user-plan-cen:not(:last-child){
        padding: 0 0 0.5rem;
      }
      .user-plan-date{
        padding: 0 0 0.5rem;
        font-size: 0.9rem;
        font-weight: 600;
      }
      .user-main-plan{
        .user-plan-model{
          display: flex;
          &:not(:last-child){
            margin: 0 0 1.2rem;
            .user-plan-model-l{
              >div{
                >div{
                  display: block;
                }
              }
            }
          }
          .user-plan-model-l{
            -webkit-box-flex: 1;
            -ms-flex: 1;
            flex: 1;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            align-items: center;
            justify-content: center;
            >span{
              font-size: 0.7rem;
            }
            >div{
              width: 0.3rem;
              height: 0.3rem;
              background: #378aff;
              font-size: 0.28rem;
              border-radius: 50%;
              margin: 0 0 0 0.5rem;
              position: relative;
              >div{
                color: #d8d8d8;
                position: absolute;
                top: 0.4rem;
                -webkit-writing-mode: tb-rl;
                -ms-writing-mode: tb-rl;
                writing-mode: tb-rl;
                display: none;
              }
            }
          }
          .user-plan-model-r{
            -webkit-box-flex: 3;
            -ms-flex: 3;
            flex: 3;
            background: #3a3a3a;
            border-radius: 0.3rem;
            padding: 0.2rem 0;
            >h2{
              margin: 0;
              padding: 0.5rem 0.5rem 0 0.5rem;
              font-size: 0.8rem;
            }
            >p{
              margin: 0;
              padding: 0.5rem;
              font-size: 0.7rem;
            }
          }
        }
      }
    }
    .user-plan-add{
      width: 1.3rem;
      height: 1.3rem;
      border-radius: 50%;
      background: #747474;
      position: fixed;
      bottom: 1rem;
      right: 1rem;
      /*box-shadow: 0 0 0.2rem #fffafac7;*/
      display: flex;
      align-items: center;
      justify-content: center;
      >div{
        font-size: .7rem;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        img{
          width: 1.1rem;
          height: 1.1rem;
          border-radius: 50%;
        }
      }
    }
    .user-pop-main{
      color: #000;
      margin: 1.5rem 0 0.5rem;
      padding: 0.5rem;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      >div{
        cursor: pointer;
      }
    }
    .model-main {
      margin-top: 2rem;
      .van-cell {
        cursor: pointer;
        background: #ed8d48;
        color: #fff;
      }
      .van-cell__value {
          color: #efff00;
      }
    }
    /deep/ .van-popup{
      border-radius: 0.3rem;
      width: 80%;
    }
    /deep/ .van-popup__close-icon--top-right{
      top: 0.2rem;
      right: 0.2rem;
    }
    /deep/ .van-popup__close-icon{
      font-size: 1rem;
    }
    /deep/ .van-button{
      height: 2rem;
      margin: 15px 0 0;
    }
    /deep/ .van-button--round{
      border-radius: 0.3rem;
    }
    /deep/ .van-button--info {
      color: #fff;
      background-color: #378aff;
    }
  }
</style>



