import router from './router'
import NProgress from 'nprogress' // Progress 
import 'nprogress/nprogress.css'// Progress 
import { getToken } from '@/assets/js/auth' // 
import { Message } from 'element-ui'

const whiteList = ['/login'] // 

router.beforeEach((to, from, next) => {
  // Progress
  NProgress.start()
  // token  -  1.login2.; 2-1:2-2
  // token  - 1.= login,2.!=login, login
  if (getToken()) {
    if (to.path === '/login') {
      Message.success('token ')
      // next({ path: '/' })
      NProgress.done()
    } else { // login--
      if (false) { // 
        // ......
        try{
          next({ path: '/' })
        } catch (e) {
          console.log(e)
        }
      } else { // 
        next()
      }
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) { // =login-login
      next()
    } else { // loginlogin
      next('/login')
      NProgress.done()
    }
  }
})

router.afterEach(() => {
  NProgress.done() // Progress
})



