<template>
  <div class="star">
    <span class="star-item" v-for="(item,index) in starClasses" :key="index" :class="item"></span>
  </div>
</template>

<script>
  // 
  const CLASS_ON = 'on'
  const CLASS_HALF = 'half'
  const CLASS_OFF = 'off'

  export default {
    props: {
      score: Number,
      size: Number
    },
    data() {
      return{
        list: ['on','on','half','off','off'] // 
      }
    },
    computed: {
      /*
      3.2: 3 + 0 + 2
      3.5: 3 + 1 + 1
       */
      starClasses () {
        const {score} = this
        const scs = []
        // scsnCLASS_ON
        const scoreInteger = Math.floor(score)
        //  
        for (let i = 0; i < scoreInteger; i++) {
          scs.push(CLASS_ON)
        }
        // scs0/1CLASS_HALF
        // >0.5  
        if(score*10-scoreInteger*10>=5) {
          scs.push(CLASS_HALF)
        }
        // scsnCLASS_OFF
        // 5
        while(scs.length<5) {
          scs.push(CLASS_OFF)
        }
        return scs
      }
    },
    created() {
      // this.starClasses()
      // console.log(this.score)
    }
  }
</script>

<style scoped lang="scss">
  .star{
    .star-item{
      width: 0.6rem;
      height: 0.6rem;
      display: inline-block;
      background-repeat: no-repeat;
      background-size: cover;
      margin-right: 0.1rem;
      &.on{
        background-image: url("./images/star24_on@2x.png");
      }
      &.off{
        background-image: url("./images/star24_off@2x.png");
      }
      &.half{
        background-image: url("./images/star24_half@2x.png");
      }
    }
  }
</style>



