<template>
  <div class="game-main">
    <!--  -->
    <div v-for="(item,index) in datas.list" :key="index">
        <div class="card-main" :class="item.className" v-if="item.isShow">
            <div class="card">
                <p>
                    {{item.cen}}
                </p>
            </div>
            <div class="select">
                <van-button v-for="(ite,ind) in item.select" :key="ind" @click="getSelect(item,ite,index)" type="info">
                    {{ite.name}}
                </van-button>
            </div>
            <div class="finish" v-if="item.isFinish"></div>
        </div>
    </div>
    <!--  -->
    <div class="model-look" v-if="isModelBut">
        <van-button type="info" @click="isModel=true"></van-button>
        <van-button type="primary" class="next" @click="toNext()"></van-button>
    </div>
    <!--  -->
    <van-popup v-model="isModel" round :close-on-click-overlay="false" :duration="0.5" :style="{ height: '60%',width: '80%' }">
        <div class="model-main">
            {{datas.level}}<br/>
            {{energy}}<br/>
            {{datas.grade}}
            <div class="model-img">
                <img src="https://liuer1211.github.io/vue_-simplicity/static/img/game/card/s1.jpg"/>
            </div>
            <van-button type="info" @click="isModel=false"></van-button>
        </div>
    </van-popup>
    <!--  -->
     <div class="canvas-content">
        <div id="content" class="canvas-container-vertical">
            <img src="../../assets/img/game/card/s1.jpg" alt="" id="lapm">
            <canvas id="signCanvas"></canvas>
        </div>
    </div>
    
  </div>
</template>

<script>
  export default {
    components: {
    },
    computed:{
    },
    watch: {
    },
    data () {
      return {
            datas: {
                list: [
                {
                    cen: "1.?",
                    select: [
                        {
                            code: "1",
                            name: "A.",
                            answer: true
                        },
                        {
                            code: "2",
                            name: "B.",
                            answer: false
                        }
                    ],
                    className: "card-one",
                    // isShow: true,
                    // isFinish: false
                },
                {
                    cen: "2.",
                    select: [
                        {
                            code: "1",
                            name: "A.",
                            answer: false
                        },
                        {
                            code: "2",
                            name: "B.",
                            answer: true
                        }
                    ],
                    className: "card-two",
                    // isShow: false
                },
                {
                    cen: "3.''",
                    select: [
                        {
                            code: "1",
                            name: "A.",
                            answer: true
                        },
                        {
                            code: "2",
                            name: "B.",
                            answer: false
                        }
                    ],
                    className: "card-three",
                    // isShow: false
                }
                ],
                level: 1, // 1 2 3
                grade: "" //   
            },
            num: 0, // 
            energy: 0, // 
            isModel: false, // 
            isModelBut: false, // 
            imgsrc: '',
            type: 0,
        }
    },
    methods: {
        // 
        toast(flag,index) {
            if (flag) {
                this.$toast(
                    {
                        type: 'success',
                        message: '',
                        duration: 1000
                    }
                );
            } else {
                this.$toast(
                    {
                        type: 'fail',
                        message: '',
                        duration: 1000
                    }
                );
            }
        },
        // but
        getSelect(item,ite,index) {
            // 
            if (!item.isFinish) {
                // 
                if (ite.answer) {
                    this.toast(ite.answer,index);
                    this.num++;
                    this.energy = this.num+this.energy*2;
                } else {
                    this.toast(ite.answer,index);
                }
                // ,list
                this.getNewDatas(index);
            }
        },
        // ,list
        getNewDatas(index) {
            // 
            this.datas.list.map(
                () => {
                    this.datas.list[index].isFinish=true;
                    // console.log(index,this.datas.list.length);
                    if (this.datas.list.length-1 !== index) {
                        this.datas.list[index+1].isShow=true;
                    }
                }
            );
            console.log(this.datas);
            // 
            if (this.datas.list.length-1 === index) {
                setTimeout(()=>{
                    this.isModel = true;
                    this.isModelBut = true;
                },1500);
            }
        },
        // list
        getDatas() {
            this.datas.list.map(
                item => {
                    item.isShow = item.className === "card-one" ? true:false;
                    item.isFinish = false;
                }
            )
            // 
            this.datas = JSON.stringify(this.datas);
            this.datas = JSON.parse(this.datas);
            // console.log(this.datas)
        },
        // 
        toNext() {
            // 
            this.toReset();
            // 
            this.addDatas();
        },
        // 
        toReset() {
            this.datas = {} // 
            this.num = 0, // 
            this.energy = 0, // 
            this.isModel = false, // 
            this.isModelBut = false // 
        },
        // 
        addDatas() {
            this.datas={
                list: [
                {
                    cen: "1.?",
                    select: [
                        {
                            code: "1",
                            name: "A.",
                            answer: true
                        },
                        {
                            code: "2",
                            name: "B.",
                            answer: false
                        }
                    ],
                    className: "card-one",
                    // isShow: true,
                    // isFinish: false
                },
                {
                    cen: "2.",
                    select: [
                        {
                            code: "1",
                            name: "A.",
                            answer: false
                        },
                        {
                            code: "2",
                            name: "B.",
                            answer: true
                        }
                    ],
                    className: "card-two",
                    // isShow: false
                },
                {
                    cen: "3.''",
                    select: [
                        {
                            code: "1",
                            name: "A.",
                            answer: true
                        },
                        {
                            code: "2",
                            name: "B.",
                            answer: false
                        }
                    ],
                    className: "card-three",
                    // isShow: false
                }
                ],
                level: 2, // 1 2 3
                grade: "" //   
            },
            this.getDatas();
        },
        // 
        greenType() {
            this.initCanvas(window.orientation);
        },
        // 
        initCanvas(type) {
            let contentBox=document.getElementById('content');
            let oCanvas = document.getElementById('signCanvas');
            oCanvas.width = contentBox.clientWidth;
            oCanvas.height = contentBox.clientHeight;
            // 
            let ctx = oCanvas.getContext('2d');
            // 
            ctx.fillStyle = "#ccc";
            // // fillStyle.
            ctx.fillRect(0, 0, oCanvas.width, oCanvas.height);
            // 
            ctx.fillStyle = "#fff"
            ctx.font = "30px Arial"
            // 
            ctx.fillText('', oCanvas.width/2-40,oCanvas.height/2);
            
            /*  
            let img = new Image();
            img.src = require('../img/test1.jpg');
            img.width= oCanvas.width;
            img.height = oCanvas.height;
            img.nlad=function(e){
                let pattern=ctx.createPattern(img,'no-repeat')
                ctx.fillStyle=pattern;
                ctx.fillRect(0,0,oCanvas.width,oCanvas.height)
            }*/

            //
            oCanvas.addEventListener('touchstart',function(ev){
                ev=ev||event;
                let touchC=ev.changedTouches[0];//
                // clientXclientY
                //offsetLeftabsoluteoffsetTopabsolute 
                // absolutebody
                let x=touchC.clientX-contentBox.offsetLeft; //contentBox
                let y=touchC.clientY-contentBox.offsetTop;  //contentBox

                ctx.save(); // 
                //
                ctx.globalCompositeOperation='destination-out'
                // 
                ctx.lineWidth=30;
                // lineCap  butt	
                ctx.lineCap="butt";
                // lineJoin miter	
                ctx.lineJoin="miter";
                // miterLimit 
                ctx.miterLimit= 5;
                // 
                ctx.beginPath();
                //
                ctx.moveTo(x,y);
                ctx.lineTo(x+1,y+1);
                // 
                ctx.stroke();
                // 
                ctx.restore();
            });
            
            oCanvas.addEventListener('touchmove',function(ev){
                ev=ev||event;
                let touchC=ev.changedTouches[0];//
                //
                let x=touchC.clientX-contentBox.offsetLeft;
                let y=touchC.clientY-contentBox.offsetTop;

                ctx.save();
                ctx.globalCompositeOperation='destination-out'
                ctx.lineWidth=30;
                ctx.lineCap="butt";
                ctx.lineJoin="miter";
                ctx.miterLimit= 5;
                ctx.lineTo(x+1,y+1);
                ctx.stroke();
                ctx.restore();
            });

            let flag=0;
            //
            oCanvas.addEventListener('touchend',function(){
                let imgdatga=ctx.getImageData(0,0,oCanvas.width,oCanvas.height);
                //
                let allPx=imgdatga.width*imgdatga.height;
                console.log(imgdatga);
                for(let i=0;i<allPx;i++){
                    if(imgdatga.data[4*i+3]==0){
                        flag++;
                    }
                }
                //canvascanvas0
                // if(flag>=allPx/2){
                //     oCanvas.style.opacity=0;
                // }
                //0canvas
                oCanvas.addEventListener('transitionend',function(){
                    this.remove();//canvas
                });
            });

        }, 
    },
    created() {
        this.getDatas();
    },
    mounted(){
        // 
        this.greenType();
    },
    beforeDestroy(){
    }
  }
</script>

<style scoped lang="scss">
    .canvas-content {
        // position: fixed;
        // top: 0;
        // left: 0;
        width: 100%;
        height: 100%;
        z-index: 1111111;
        background: #bbb;
        .vertical-text {
            p {
                color: #ddd;
                font-size: 20px;
                margin: 40px 0;
                text-align: center;

            }
        }
        .canvas-operation {
            width: 100%;
            height: 40px;
            position: fixed;
            bottom: 0;
            left: 0;
            z-index: 111111111;
            background: #ccc;
            button {
                width: 70px;
                height: 40px;
                background: #aaa;
                margin: 0 10px;
                outline: none;
                &:active {
                    background: #888;
                }
            }
        }
        .canvas-container {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 40px;
            background: green;
            #signCanvas {
                width: 100%;
                height: calc(100% - 40px);
                background: #fff;
                border: none;
                box-sizing: border-box;
                overflow: hidden;
                position: fixed;
                top: 0;
                left: 0;
                z-index: 1111111112;
            }
        }
        .canvas-container-vertical {
            width: 100%;
            height: 300px;
            position: relative;
            top: 0;
            left: 0;
            font-size: 32px;
            color: red;
            line-height: 300px;
            text-align: center;
            #lapm {
                width: 100%;
                height: 100%;
            }
            #signCanvas {
                width: 100%;
                height: 100%;
                // background: #fff;
                border: none;
                box-sizing: border-box;
                overflow: hidden;
                position: absolute;
                top: 0;
                left: 0;
                bottom: 40px;
                z-index: 1111111112;
            }
        }
    }
    .game-main{
        color: #000;
        padding: 0.8rem;
        font-size: 0.8rem;
        >div:not(:first-child) {
            >div{
                margin: 0.8rem 0 0 0;
            }
        } 
        .card-main{
            position: relative;
            padding: 0.8rem;
            background-color: #fff;
            border-radius: 0.2rem;
            box-shadow: -1px 1px 3px #ebedf0;
            border: 1px solid #e5e5e5;
            .card {
                margin-bottom: 1rem;
                padding: 0.8rem;
                background-color: #fff;
                border-radius: 0.2rem;
                box-shadow: 1px 2px 3px 2px #dfdddd;
                p {
                    margin: 0;
                    line-height: 1rem;
                    letter-spacing: 1px;
                    word-break: break-all;
                }
            }
            .select {
                display: flex;
                justify-content: space-between;
                align-items: center;
                .van-button {
                    cursor: pointer;
                    height: 1.8rem;
                    border-radius: 0.2rem;
                    box-shadow: 1px 1px 3px 0px #70a7f9;
                }
                .van-button--normal {
                    padding: 0 2rem;
                }
            }
            .finish {
                position: absolute;
                top: 4px;
                right: 4px;
                width: 2rem;
                height: 2rem;
                background: #c9c2c2;
                color: #f00;
                border-radius: 50%;
                line-height: 2rem;
                text-align: center;
                opacity: .8;
                font-size: .7rem;
                font-weight: 600;
            }
        }
        .model-look {
            margin: 1rem 0 0;
            .van-button {
                cursor: pointer;
                height: 1.8rem;
                border-radius: .2rem;
                width: 100%;
                box-shadow: 1px 1px 3px 0px #70a7f9;
            }
            .next {
                margin: 0.8rem 0 0;
            }
        }
        .model-main {
            padding: .8rem;
            .model-img {
                width: 100%;
                height: 10rem;
                margin: 1rem 0;
                display: flex;
                align-items: center;
                justify-content: center;
                img {
                    width: 100%;
                    height: 100%;
                    display: block;
                    object-fit: cover;
                    border-radius: 0.2rem;
                }
            }
            .van-button {
                height: 1.8rem;
                border-radius: .2rem;
                width: 100%;
                box-shadow: 1px 1px 3px 0px #70a7f9;
            }
        }
    }
    /*  */
    .card-one {
        animation: anim 1.5s;
    }
    @keyframes anim { 
        0% {opacity: 0;}
        50% {opacity: 0.5;}
        100%{opacity:1;}
    }
    /*  */
    .card-two {
        animation: bounceInLeft 1s cubic-bezier(0.215, 0.610, 0.355, 1.000)
    }
    @keyframes bounceInLeft {
        0% {
            opacity: 0;
            transform: translate3d(-3000px, 0, 0);
        }
        60% {
            opacity: 1;
            transform: translate3d(25px, 0, 0);
        }
        75% {
            transform: translate3d(-10px, 0, 0);
        }
        90% {
            transform: translate3d(5px, 0, 0);
        }
        100% {
            transform: none;
        }
    }
    /*  */
    .card-three {
        animation-duration: 1.5s !important;
        -webkit-animation-duration: 1.5s !important;
        animation-fill-mode: both !important;
        -webkit-animation-fill-mode: both !important;
        animation-name: fadeInRight;
        -webkit-animation: fadeInRight;
    }
    /*-start*/
    .animated {
        animation-duration: 1.5s !important;
        -webkit-animation-duration: 1.5s !important;
        animation-fill-mode: both !important;
        -webkit-animation-fill-mode: both !important;
    }
    .animated02 {
        animation-duration: 1s !important;
        -webkit-animation-duration: 1s !important;
        animation-fill-mode: both !important;
        -webkit-animation-fill-mode: both !important;
    }
    /**/
    .toShow {
        position: absolute;
        animation: toShow 1.5s 0.5s;
        animation: toShow 1.5s 0.5s;
        animation-fill-mode: both !important;
        -webkit-animation-fill-mode: both !important;
    }
    /* */
    .enlarge {
        animation-name: enlarge;
        -webkit-animation: enlarge;
    }
    /**/
    .fadeInDown {
        animation-name: fadeInDown;
        -webkit-animation: fadeInDown;
    }
    /**/
    .fadeInUpLeft {
        animation-name: fadeInUpLeft;
        -webkit-animation: fadeInUpLeft;
    }
    /**/
    .fadeInRight {
        animation-name: fadeInRight;
        -webkit-animation: fadeInRight;
    }
    /**/
    .fadeInLeft {
        animation-name: fadeInLeft;
        -webkit-animation: fadeInLeft;
    }
    /**/
    .coreRotate {
        animation-name: coreRotate;
        -webkit-animation-name: coreRotate;
    }

    @keyframes toShow {
        0% {opacity: 0;}
        100% {opacity: 1;}
    }
    @-webkit-keyframes toShow {
        0% {opacity: 0;}
        100% {opacity: 1;}
    }

    @keyframes enlarge
    {
        from {
            opacity: 0;
            -ms-transform: scale(0.1,0.1); /* IE 9 */
            -webkit-transform: scale(0.1,0.1); /* Safari */
            transform: scale(0.1,0.1); /*  */
        }
        to {
            opacity:1;
            -webkit-transform:scale(1,1);
            transform:scale(1,1);
        }
    }
    
    @-webkit-keyframes enlarge /* Safari  Chrome */
    {
        from {
            opacity:0;
            -ms-transform: scale(0.1,0.1); /* IE 9 */
            -webkit-transform: scale(0.1,0.1); /* Safari */
            transform: scale(0.1,0.1); /*  */
        }
        to {
            opacity:1;
            -webkit-transform:scale(1,1);
            transform:scale(1,1);
        }
    }

    /**/
    @keyframes fadeInDown
    {
        from {
            opacity: 0;
            -webkit-transform: translate(0,-1000px); /* Safari */
            transform: stranslate(0,-1000px); /*  */
        }
        to {
            opacity:1;
            -webkit-transform: translate(0,10px); /* Safari */
            transform: stranslate(0,10px); /*  */
        }
    }
    
    @-webkit-keyframes fadeInDown /* Safari  Chrome */
    {
        from {
            opacity:0;
            -webkit-transform: translate(0,-1000px); /* Safari */
            transform: stranslate(0,-1000px); /*  */
        }
        to {
            opacity:1;
            -webkit-transform: translate(0,10px); /* Safari */
            transform: stranslate(0,10px); /*  */
        }
    }
    /**/
    @keyframes fadeInUpLeft
    {
        from {
            opacity: 0;
            -webkit-transform: translate(-1000px,1000px); /* Safari */
            transform: stranslate(-1000px,1000px); /*  */
        }
        to {
            opacity:1;
            -webkit-transform: translate(0,10px); /* Safari */
            transform: stranslate(0,10px); /*  */
        }
    }
    
    @-webkit-keyframes fadeInUpLeft /* Safari  Chrome */
    {
        from {
            opacity:0;
            -webkit-transform: translate(-1000px,1000px); /* Safari */
            transform: stranslate(-1000px,1000px); /*  */
        }
        to {
            opacity:1;
            -webkit-transform: translate(0,10px); /* Safari */
            transform: stranslate(0,10px); /*  */
        }
    }
    /**/
    @keyframes fadeInRight
    {
        from {
            opacity: 0;
            -webkit-transform: translate(1000px,0); 
            transform: stranslate(1000px,0); 
        }
        to {
            opacity:1;
            -webkit-transform: translate(0px,0); 
            transform: stranslate(0px,0); 
        }
    }
    
    @-webkit-keyframes fadeInRight 
    {
        from {
            opacity:0;
        -webkit-transform: translate(1000px,0); 
            transform: stranslate(1000px,0); 
        }
        to {
            opacity:1;
            -webkit-transform: translate(10px,0); 
            transform: stranslate(10px,0); 
        }
    }
    /**/
    @keyframes fadeInLeft
    {
        from {
            opacity: 0;
            -webkit-transform: translate(-1000px,0); 
            transform: stranslate(-1000px,0); 
        }
        to {
            opacity:1;
            -webkit-transform: translate(10px,0); 
            transform: stranslate(10px,0); 
        }
    }
    
    @-webkit-keyframes fadeInLeft 
    {
        from {
            opacity:0;
        -webkit-transform: translate(-1000px,0); 
            transform: stranslate(-1000px,0); 
        }
        to {
            opacity:1;
            -webkit-transform: translate(10px,0); 
            transform: stranslate(10px,0); 
        }
    }

    /**/
    @keyframes coreRotate {
        5% {
            transform:rotate(5deg);
            -ms-transform:rotate(5deg); /* IE 9 */
            -webkit-transform:rotate(5deg); /* Safari and Chrome */
        }
        30% {
            transform:rotate(-5deg);
            -ms-transform:rotate(-5deg); 
            -webkit-transform:rotate(-5deg); 
        }
        60% {
            transform:rotate(5deg);
            -ms-transform:rotate(5deg); 
            -webkit-transform:rotate(5deg);
        }
        90% {
            transform:rotate(-5deg);
            -ms-transform:rotate(-5deg); 
            -webkit-transform:rotate(-5deg); 
        }
    }
    @-webkit-keyframes coreRotate {
        5% {
            transform:rotate(5deg);
            -ms-transform:rotate(5deg); /* IE 9 */
            -webkit-transform:rotate(5deg); /* Safari and Chrome */
        }
        30% {
            transform:rotate(-5deg);
            -ms-transform:rotate(-5deg); 
            -webkit-transform:rotate(-5deg); 
        }
        60% {
            transform:rotate(5deg);
            -ms-transform:rotate(5deg); 
            -webkit-transform:rotate(5deg);
        }
        90% {
            transform:rotate(-5deg);
            -ms-transform:rotate(-5deg); 
            -webkit-transform:rotate(-5deg); 
        }
    }
    /*-end*/
</style>



