// 
export const dateUtil = {
  // yyyy-MM-dd
  formatDateStr: (date) => {
    let MM = '',
      DD = '';
    if (date.getMonth() > 8) {
      MM = (date.getMonth() + 1).toString();
    } else {
      MM = '0' + (date.getMonth() + 1).toString();
    }
    if (date.getDate() > 9) {
      DD = date.getDate().toString();
    } else {
      DD = '0' + date.getDate().toString();
    }
    return date.getFullYear() + '-' + MM + '-' + DD;
  },
  //   https://www.jb51.net/article/74614.htm  https://www.cnblogs.com/liubingyjui/p/10820491.html
  toChangeStr(val) { //  10:00 -1000   01:00 - 100  01 - 1 22 - 22
    // 
    val = val.toString()
    // : 
    let list = val.split(":")
    let newStr = ''
    list.forEach((v,index) => {
      let str = v.substr(0,1) // startlength
      let st = ''
      // 
      if (index === 0) {
        // 0  01 02  0  1 2 3
        if (str === '0') { //  int
          st = v.substr(1, v.length)
        } else { //  int
          st = v
        }
      } else {
        st = v
      }
      // 
      newStr = newStr + st
    });
    // int 
    return newStr
  }
}

// element-ui 
export const eleUtil = {
  // 
  // this.toChangeTip('error','!')
  toChangeTip(data1 = 'error', data2 = '', data3 = '2000', data4 = true, data5 = '30'){
    this.$message({
      type: data1, // 
      message: data2, // 
      duration: data3, // 
      center: data4, // is
      offset: data5 //  px
    });
  }
}



